import React, { useState, useEffect } from 'react'
import axios from 'axios'

const TestMulti = (props) => {

    const url = `https://jsonplaceholder.typicode.com/posts`;

    const [result, setResult] = useState(null);
    let content = null

    useEffect(() => {
        axios.get(url)
            .then(response => {
                setResult(response.data);
            })
    }, [url])

    //console.log(url);

    
    if (result) {
        content =
            <div>
            <h1> {result[0].userId}</h1>
            <p> {result[0].id} </p>
            <p> {result[0].title}</p>
            <p> {result[0].body}</p>
        </div>
    }


    return (
        <div>
        {content}
        </div>
    )

}

export default TestMulti;